@media only screen and (max-width: 1024px) { 
    .msg-container {
        min-width: 600px !important;
    }
}

@media only screen and (max-width: 768px) { 
    .msg-container {
        min-width: unset !important;
    }

    .messages-container {
        padding: 2rem !important;
        width: 100%;
    }
}

.messages-container {
    padding: 4rem;
}

.msg-container {
    width: auto;
    padding: 1rem;
    margin: 1rem 0rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 800px;
}

.msg-container:hover{
    color: #31344b;
    background-color: #e6e7ee;
    border-color: #e6e7ee;
    box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #fff;
}

.msg-container img {
    width: 40px;
    height: 40px;
    padding-left: 2rem;

}
